.border-container {
    padding: 40px;
}

.border-top, 
.border-bottom {
  border: 3px solid #AF8B52;
  height: 12px;
  position: relative;
}

.border-top:before, 
.border-top:after, 
.border-bottom:before, 
.border-bottom:after {
  content: "";
  position: absolute;
  border: 3px solid #AF8B52;
  width: 20px;
  height: 20px;
}

.border-top:before {
    border-radius: 0 99%;
    left: -20px;
    top: -20px;
}
.border-top:after {
    border-radius: 99% 0;
    right: -20px;
    top: -20px;
}
.border-bottom:before {
    border-radius: 99% 0;
    left: -20px;
    bottom: -20px;
}
.border-bottom:after {
    border-radius: 0 99%;
    right: -20px;
    bottom: -20px;
}
.border-center {
    border-left: 3px solid #AF8B52;
    border-right: 3px solid #AF8B52;
}

