header {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
}

header .logo {
    text-align: center;
    margin-top: 20px;
    margin-bottom: 20px;
}

header .logo img {
    max-width: 60vw;
}

header .links {
    display: flex;
    margin-bottom: 30px;
    justify-content: center;
}

header .links a {
    text-decoration: none;
    color: #f5eee3;
    font-size: 20px;
    font-weight: bold;
    padding-left: 20px;
    padding-right: 20px;
    padding-bottom: 5px;
    font-family: 'Mason Chronicles';
    outline: none;
}

@media(max-width: 768px) {
    header .links a {
        font-size: 14px;
    }
}

.active {
    border-bottom: solid 5px #f5eee3;
}

@font-face {
    font-family: 'Mason Chronicles';
    src: url('./fonts/MasonChronicles.eot');
    font-weight: 100;
    font-style: normal;
}
@font-face {
    font-family: 'Mason Chronicles';
    src: local('MasonChronicles'),
        url('data:application/font-woff2;charset=utf-8;base64,') format('woff2'),
        url('data:application/font-woff;charset=utf-8;base64,') format('woff'),
        url('./fonts/MasonChronicles.ttf') format('truetype');
    font-weight: 100;
    font-style: normal;
    font-display: swap;
}